import { all, fork, takeLatest, call, put, take } from 'redux-saga/effects';
import { LOG_IN_SUCCESS, LOG_IN_FAILURE, LOG_IN } from '../reducers/user';

// all -> redux-saga에서 eventListener와 비슷하게 while(true)를통해  실행대기 함수등록을 하였다면, 모두 묶어줄 때 사용한다.
// take -> redux-saga에서 generator.next()와 마찬가지로 yield 로인해 중단된 함수를 다시 재개시킨다.

const HELLO_SAGA = 'HELLO_SAGA';

function* loginAPI() {
  // 서버에 요청을 보내는 부분
}

function* login() {
  try {
    yield call(loginAPI);
    yield put({ // loginAPI 성공 put은 dispatch와 동일
      type: LOG_IN_SUCCESS
    });
  } catch (e) { // loginAPI 실패
    console.error(e);
    yield put({
      type: LOG_IN_FAILURE
    });
  }
}
function* watchSignUp() {
  yield takeLatest(LOG_IN, login)
}

function* watchLogin() {
  yield takeLatest(LOG_IN, login)
}

function* helloSaga() {
  console.log('before saga');
  yield take(HELLO_SAGA);
  console.log('hello saga');
}

export default function* userSaga() {
  yield all([
    watchHello(),
    watchLogin(),
    waatchSignUp()
  ]);
}